}
.mygradient{
  background-color: #d400b1;
}
.my-a{
  transition: all ease-in 0.3s;
  -webkit-transition: all ease-in 0.3s;
  -moz-transition: all ease-in 0.3s;
  -ms-transition: all ease-in 0.3s;
  -o-transition: all ease-in 0.3s;
  padding-bottom: 3px;
}
.my-a:hover {
  border-bottom: 2px solid rgb(172, 24, 158);
  transition: all ease-in 0.14s;
  -webkit-transition: all ease-in 0.14s;
  -moz-transition: all ease-in 0.14s;
  -ms-transition: all ease-in 0.14s;
  -o-transition: all ease-in 0.14s;
}
.load-container {
  width: 200px;
  height: 100px;
  position: absolute;
  margin: 0 auto;
  z-index: 1000;
  left: 50%;
  top: 50%;
  margin-left: -100px;
  margin-top: -50px;
}
#blur-effect-theme {
  @apply bg-white/10 bg-clip-padding backdrop-filter backdrop-blur bg-opacity-50 opacity-100 z-[100] shadow-sm shadow-slate-100 transition-all duration-300 shadow-xl;
}
#black{
  color:black;
}
.ico {
  width: 40px;
  height: 40px;
  background-color: rebeccapurple;
  padding: 10px 10px;
  color: #fff;
  position: absolute;
  border-radius: 50%;
  border: 1px solid transparent;
  box-shadow: 1px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

.ico-one {
  background-color: deeppink;
  animation: bouncing 0.5s ease infinite alternate both;
}

.ico-two {
  margin-left: 60px;
  background-color: dodgerblue;
  animation: bouncing-two 0.5s ease infinite alternate both;
}

.ico-three {
  margin-left: 120px;
  animation: bouncing 0.5s ease infinite alternate both;
}

@keyframes bouncing {
  0% {
    bottom: 0;
  }

  100% {
    bottom: 30px;
  }
}

@keyframes bouncing-two {
  0% {
    bottom: 30px;
  }

  100% {
    bottom: 0;
  }
}
#drop-down{
  display: inline-block !important;
}
#user-name{
  font-size:13px !important;
  font-weight: bold;
}
@media screen and (max-width:600px){
  #my-logo{
    display: inline-flex !important;
    justify-self: end;
  }
}
